import React from "react";
import {
    SiWebpack,
    AiFillTwitterCircle,
    AiOutlineMedium,
    FaTelegramPlane,
    FiMail
  } from "react-icons/all";

import PropTypes from "prop-types";

const SocialLinkinCard2 = ({ size }) => {
  return (
    <div className="social-links2">
      <a href="https://twitter.com/mambapad_bsc">
        <AiFillTwitterCircle className="social-link" />
      </a>
      <a href="https://t.me/mambapad_official">
        <FaTelegramPlane className="social-link" />
      </a>
      <a href="https://t.me/mambapad_ann">
        <FaTelegramPlane className="social-link" />
      </a>
      <a href="https://medium.com/@mambapad">
        <AiOutlineMedium className="social-link" />
      </a>
      <a href="mailto:support@mambapad.com">
        <FiMail className="social-link" />
      </a>
    </div>
  );
};

SocialLinkinCard2.propTypes = {
  size: PropTypes.number.isRequired,
};

export default SocialLinkinCard2;
