import MambapadAbi from '../contracts/Mambapad.json';
import { useContract } from './useContract';

export function useMambapadApi() {
  const contractAddress = MambapadAbi.address;
  const { functions } = useContract(contractAddress, MambapadAbi.abi);
  return {  
    setIsFeeExempt:  functions.setIsFeeExempt,

  };
}
