import React, { useState, useEffect } from "react";
import Countdown from "react-countdown";
import PropTypes from "prop-types";

import SocialLinkinCard from "../SocialLinkinCard";
import SocialLinkinCard2 from "../SocialLinkinCard2";

export default function ComingCard({ project }) {
  // const startTime = new Date(1643673600000).toUTCString();//1643932800000
  const [isOpen, setIsOpen] = useState(false);
  const startTimeEpoch = new Date(project.startTime) * 1.0;
  const startTime = new Date(project.startTime * 1.0).toUTCString();
  const timeOut = startTimeEpoch - Date.now();
  const getLocalTime = (s) => localStorage.getItem(s) * 1.0;
  setTimeout(() => {
    // console.log('now is the time')
    setIsOpen(true);
  }, timeOut);

  useEffect(() => {
    console.log("starttime", startTime, startTimeEpoch);
    localStorage.setItem("start_epoch_time_comingpool", startTimeEpoch);
  }, [startTimeEpoch]);

  return (
    <React.Fragment>
      {!isOpen ? (
        <section className="coming-card">
          <div className="coming-card-header">
            <a href="/">
              <img src={project.picture} alt="project profile"></img>
            </a>
            <div className="coming-card-title">
              <strong>{project.name}</strong>
              <strong>{project.subname}</strong>
              <SocialLinkinCard />
            </div>
          </div>
          <SocialLinkinCard2 />
          <div className="coming-card-body">{project.message}</div>
          <div className="coming-card-info">
            <p>min: 0.1 BNB</p>
            <p>max: 2.4 BNB</p>
          </div>
          <div className="coming-card-footer">
            <p>
              {`START at: `}
              {startTime}
            </p>
            <div style={{ marginBottom: 0, textAlign: "center" }}>
              <div>{`Pool will open after    `}</div>
              <span style={{ fontSize: "40px" }}>
                <Countdown
                  date={
                    localStorage.getItem("start_epoch_time_comingpool") * 1.0
                  }
                  fontSize={30}
                  daysInHours
                />
              </span>
            </div>
          </div>
        </section>
      ) : (
        <></>
      )}
    </React.Fragment>
  );
}

ComingCard.propTypes = {
  project: PropTypes.object.isRequired,
};
