        setInfo(information);
        if (info.startingTime) {
          const time = info.startingTime.toString() * 1.0 * 1000;
          setStartTime(time);
          setUtctime(new Date(time).toUTCString());
        }
        if (info.endingTime) {
          setEndTime(info.endingTime.toString() * 1.0 * 1000);
        }
        if (info.mampAmountofContract) {
          setContractMampBalance(
            ethers.utils.formatUnits(info.mampAmountofContract.toString(), "8")
          );
        }
