    localStorage.setItem("end_epoch_time", endTime);
    if (timeOut > 0) {
      setSellingNow(false);
      setFinishNow(false);
    }
    if (startTime < Date.now() && endTime > Date.now()) {
      setSellingNow(true);
      setFinishNow(false);
    }
