import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  TwitterTimelineEmbed,
} from "react-twitter-embed";
import { Badge } from "react-bootstrap";
import { FaTwitter } from "react-icons/all";
import SocialLink from "../components/SocialLink";
import logo from "../assets/img/Yellow_II.jpg";

export default class Footer extends Component {
  render() {
    return (
      <>
        <h3 className="col-12 text-center font-weight-bold sub-title">
          CONTACT US
        </h3>
        <section className="container footer">
          <div className="contactlogo">
            <Link to="/">
              <img src={logo} alt="logo"></img>
            </Link>
          </div>
          <div>
            <SocialLink size={30} />
          </div>

          <section className="footer-card">
            <div className="footer-card-header">
              <div className="footer-card-title">
                <strong>TWITTER FEED</strong>
              </div>
              <a
                className="TG-link"
                href="https://twitter.com/mambapad_bsc"
                target="blank"
                color="white"
              >
                <FaTwitter size={30} />
              </a>
            </div>
            <div className="footer-card-footer">
              <TwitterTimelineEmbed
                sourceType="url"
                url="https://twitter.com/mambapad_bsc"
                screenName="mambapad"
                options={{ height: 200 }}
              />
            </div>
          </section>
        </section>
        <hr style={{ borderColor: "#f1b90c", marginTop: "10px" }} />
        <div className="footer-info">
          <div className="footer-router">
            <div><Link to="/projects">IDO PROJECTS<Badge pill bg="danger">1</Badge></Link></div>
            <div><Link to="/staking">STAKING</Link></div>
            <div><Link to="/vesting">VESTING</Link></div>
            <div><Link to="/applyido">APPLY IDO</Link></div>
          </div>
          <div className="footer-contact">
            <p style={{ textAlign: "center", color: "grey" }}>
              Copyright © 2022, MAMBAPAD. All Right Reserved
            </p>
            <span>
              <a href="mailto:support@mambapad.com">support@mambapad.com</a>
            </span>
          </div>
        </div>
      </>
    );
  }
}
